import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';

import { AllowPermissionComponent } from './allow-permission.component';

const routes: Routes = [
  {
    path: '',
    component: AllowPermissionComponent,

    data: {
      title: "Allow Purpose",
      permission: [
        ACTION.ADD,
        ACTION.EDIT,
      ],
    }
  }
  ];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AllowPermissionRoutingModule { }
